import React, { Component } from "react";
import { AiOutlineLoading3Quarters } from "react-icons/ai";
import { BsArrowLeft, BsArrowRight, BsCalendar2Check } from "react-icons/bs";
import { MdClose } from "react-icons/md";
import { RiSearchLine } from "react-icons/ri";
import {
  AssignTraineeToTraining,
  GetTraineesToBeTrainedOnSchool,
  GetTraineesToBeTrainedOnSchoolInterface,
  GetTrainingsListByStatusInterface,
  TeachersToBeTrainedResponse,
  TrainingParticipants,
} from "../../../actions/training-offers";
import Button from "../../../components/Buttons/Button";
import Loading from "../../../components/Loading/Loading";
import { search } from "../../../utils/functions";

interface AddParticipantsProps {
  school_code: string;
  training_details: GetTrainingsListByStatusInterface;
  onClose: () => void;
  onSubmitParticipants: (participants: TeachersToBeTrainedResponse[]) => void;
  addedParticipants: TrainingParticipants[];
}
interface AddParticipantsState {
  loading: boolean;
  error: string;
  success: string;
  search_data: string;
  data: TeachersToBeTrainedResponse[] | null;
  continue: boolean;
  view_subjects: TeachersToBeTrainedResponse | null;
  adding_participant: {
    user_id: string;
    names: string;
  } | null;
  selected_trainee_category: string;
}

export const removeDuplicateParticipantsSubjects = (
  data: {
    teacher_code: string;
    course_code: string;
    course_name: string;
  }[]
): {
  teacher_code: string;
  course_code: string;
  course_name: string;
}[] => {
  const response: {
    teacher_code: string;
    course_code: string;
    course_name: string;
  }[] = [];
  for (const item of data) {
    if (
      response.find(
        (itm) => itm.course_code.toString() === item.course_code.toString()
      ) === undefined
    ) {
      response.push(item);
    }
  }
  return response;
};

export class AddParticipants extends Component<
  AddParticipantsProps,
  AddParticipantsState
> {
  constructor(props: AddParticipantsProps) {
    super(props);

    this.state = {
      loading: false,
      error: "",
      success: "",
      search_data: "",
      data: null,
      continue: false,
      view_subjects: null,
      adding_participant: null,
      selected_trainee_category:
        this.props.training_details.trainee_category.length === 0
          ? ""
          : this.props.training_details.trainee_category[0].id,
    };
  }
  // Get teachers
  LoadData = () => {
    const data: GetTraineesToBeTrainedOnSchoolInterface = {
      training_id: this.props.training_details.id,
      school_code: this.props.school_code,
      topic: this.props.training_details.topic_id,
      criteria: this.props.training_details.trainee_criteria,
    };
    this.setState({ loading: true });
    GetTraineesToBeTrainedOnSchool(
      data,
      (
        loading: boolean,
        res: {
          type: "success" | "error";
          msg: string;
          data: TeachersToBeTrainedResponse[] | null;
        } | null
      ) => {
        this.setState({ loading: loading });
        if (res !== null && res.type === "success") {
          this.setState({ data: res.data, loading: false, error: "" });
        }
        if (res !== null && res.type === "error") {
          this.setState({ data: [], loading: false, error: res.msg });
        }
      }
    );
  };
  // Add new participants
  AddNewParticipants = (participant: TeachersToBeTrainedResponse) => {
    this.setState({
      loading: false,
      adding_participant: {
        user_id: participant.user_id,
        names: `${participant.first_name} ${participant.last_name}`,
      },
    });
    AssignTraineeToTraining(
      {
        district_code:
          participant.district_code === null
            ? null
            : parseInt(participant.district_code),
        level_id: participant.teaching_level_id.toString(),
        position_code: participant.position_code,
        qualification_id: participant.qualification_id,
        school_code: parseInt(this.props.school_code),
        sector_id: participant.sector_code,
        trainee_request_id: parseInt(
          this.props.training_details.trainee_request_id
        ),
        training_id: this.props.training_details.id,
        training_to_group_id: parseInt(
          this.props.training_details.training_to_group_id
        ),
        user_id: participant.user_id,
      },
      (
        loading: boolean,
        res: {
          type: "success" | "error";
          msg: string;
        } | null
      ) => {
        // this.setState({ loading: loading });
        if (res !== null && res.type === "success") {
          // On Success
          this.props.onSubmitParticipants([participant]);
          this.setState({
            data:
              this.state.data === null
                ? []
                : this.state.data.filter((itm) => itm.nid !== participant.nid),
            success: `Participant ${participant.first_name} ${participant.last_name} has added successfully!`,
            error: "",
            loading: false,
            adding_participant: null,
          });
        }
        if (res !== null && res.type === "error") {
          this.setState({
            error: res.msg,
            success: "",
            loading: false,
            adding_participant: null,
          });
        }
      }
    );
  };
  componentDidMount(): void {
    if (this.state.data === null) {
      this.LoadData();
    }
  }
  render() {
    if (this.state.loading === true || this.state.data === null) {
      return (
        <div className="flex flex-col items-center justify-center p-6 bg-gray-100 rounded-md m-1">
          <Loading />
          <div className="text-center text-lg font-light">
            Loading, please wait...
          </div>
        </div>
      );
    }
    return (
      <div className="animate__animated animate__fadeInUp bg-white rounded-md mt-1">
        <div className="flex flex-col lg:flex-row items-center justify-between gap-2">
          <div className="flex flex-row items-center gap-3 w-full">
            {this.state.continue === false && (
              <div>
                <div
                  onClick={() => {
                    this.state.loading === false && this.props.onClose();
                  }}
                  className="bg-accent-400 hover:bg-accent-500 text-accent-900 px-3 py-2 rounded-md flex flex-row items-center justify-center gap-2 hover:text-black cursor-pointer"
                >
                  <div>
                    <BsArrowLeft className="text-xl" />
                  </div>
                  <span className="hidden md:block truncate">Back to list</span>
                </div>
              </div>
            )}
            <div className="">
              <div className="text-xl font-bold">Add training participants</div>
              <div
                className="text-sm font-semibold text-primary-800"
                // style={{ maxWidth: "500px" }}
              >
                {this.props.training_details.title}
              </div>
            </div>
          </div>
        </div>
        {/* Trainees categories */}
        <div className="my-5">
          <div className="flex flex-col md:flex-row items-center justify-between gap-2">
            <div>
              <div className="font-bold text-lg">
                List of categorized training candidates
              </div>
              <div className="text-sm text-gray-500">
                The following are trainees categories that can be added to the
                training. please select the category
              </div>
            </div>
            <div className="flex flex-row items-center justify-end gap-2">
              <div className="flex flex-col bg-gray-10 rounded px-3 text-center border pt-1">
                <div className="text-sm font-light">Assigned</div>
                <div className="font-extrabold -mt-1 text-xl">
                  {this.props.addedParticipants.length}
                </div>
              </div>
              <div className="flex flex-col bg-gray-10 rounded px-3 text-center border pt-1">
                <div className="text-sm font-light">Total</div>
                <div className="font-extrabold -mt-1 text-xl">
                  {this.props.training_details.total}
                </div>
              </div>
              {this.state.continue === true && (
                <Button
                  theme="accent"
                  className="px-3 py-2 rounded-md flex flex-row items-center justify-center gap-2 hover:text-black cursor-pointer animate__animated animate__fadeIn"
                  onClick={() => {
                    this.state.loading === false && this.props.onClose();
                  }}
                >
                  <div className="hidden md:block truncate">Continue</div>
                  <div>
                    <BsArrowRight className="text-xl" />
                  </div>
                </Button>
              )}
            </div>
          </div>
          {/* Tabs*/}
          <div className="my-3 w-full overflow-x-auto">
            <div className="flex flex-row items-end gap-0 truncate">
              {this.props.training_details.trainee_category.map((item, i) => (
                <div
                  onClick={() =>
                    this.setState({ selected_trainee_category: item.id })
                  }
                  key={i + 1}
                  className={`px-5 pt-2 pb-1 rounded-t-md ${
                    item.id === this.state.selected_trainee_category
                      ? "border-t border-l border-r bg-white text-primary-700 border-primary-300 font-semibold"
                      : "border-b border-primary-300 cursor-pointer hover:bg-primary-100 hover:text-primary-800"
                  }`}
                >
                  {item.name}
                </div>
              ))}
              <div className="border-b border-primary-300 w-full"></div>
            </div>
          </div>
        </div>
        {/* Error display */}
        {this.state.error !== "" && (
          <div
            onClick={() => this.setState({ error: "", success: "" })}
            className="bg-red-100 text-red-700 font-bold flex flex-row items-center justify-between gap-2 px-3 py-2 rounded cursor-pointer my-2 animate__animated animate__shakeX"
          >
            <div>{this.state.error}</div>
            <div>
              <MdClose className="text-2xl" />
            </div>
          </div>
        )}
        {/* Success */}
        {this.state.success !== "" && (
          <div
            onClick={() => this.setState({ error: "", success: "" })}
            className="bg-green-100 text-green-700 font-bold flex flex-row items-center justify-between gap-2 px-3 py-2 rounded cursor-pointer my-2 animate__animated animate__zoomIn"
          >
            <div>{this.state.success}</div>
            <div>
              <MdClose className="text-2xl" />
            </div>
          </div>
        )}
        {/* List of participants */}
        {/* Search */}
        <div className="flex flex-col md:flex-row md:items-center justify-between w-full gap-2 mb-2">
          <div className="relative w-full">
            <div className="absolute px-3 py-2">
              <RiSearchLine className="text-2xl text-gray-600" />
            </div>
            <input
              type="search"
              className="font-normal bg-gray-100 w-full px-3 pl-12 py-2 rounded"
              placeholder="Search..."
              value={this.state.search_data}
              onChange={(e) => this.setState({ search_data: e.target.value })}
            />
          </div>
        </div>
        {/* Title */}
        {this.state.selected_trainee_category !== "" && (
          <div className=" text-accent-900 mt-4 mb-3 font-semibold flex flex-row items-center gap-2">
            <div>
              <BsCalendar2Check className="text-2xl" />
            </div>
            <span>
              List of qualified{" "}
              {
                this.props.training_details.trainee_category.find(
                  (itm) => itm.id === this.state.selected_trainee_category
                )?.name
              }{" "}
              for this training
            </span>
          </div>
        )}
        {
          <div className="w-full overflow-x-auto mb-10">
            <table className="min-w-full text-left text-sm">
              <thead>
                <tr className="text-primary-800 truncate">
                  <th className="px-2 py-2 border border-gray-200 text-center">
                    #
                  </th>
                  <th className="px-2 py-2 border border-gray-200">
                    Full name
                  </th>
                  <th className="px-2 py-2 border border-gray-200">
                    National ID
                  </th>
                  <th className="px-2 py-2 border border-gray-200">
                    Teaching level
                  </th>
                  <th className="px-2 py-2 border border-gray-200">
                    Qualification
                  </th>
                  <th className="px-2 py-2 border border-gray-200">Position</th>
                  <th className="px-2 py-2 border border-gray-200">
                    Subjects taught
                  </th>
                  <th className="px-2 py-2 border border-gray-200">Action</th>
                </tr>
              </thead>
              <tbody>
                {search(this.state.data, this.state.search_data).length ===
                0 ? (
                  <tr>
                    <td
                      colSpan={8}
                      className="text-center text-xl font-light py-4"
                    >
                      <div className="bg-gray-100 w-full px-2 py-2 rounded">
                        No result found!
                      </div>
                    </td>
                  </tr>
                ) : (
                  (
                    search(
                      this.state.data,
                      this.state.search_data
                    ) as TeachersToBeTrainedResponse[]
                  ).map((item, i) => (
                    <tr key={i + 1} className="group">
                      <td className="px-2 py-2 border border-gray-200 text-center">
                        {i + 1}
                      </td>
                      <td className="px-2 py-2 border border-gray-200">
                        {item.first_name} {item.last_name}
                      </td>
                      <td className="px-2 py-2 border border-gray-200">
                        {item.nid}
                      </td>
                      <td className="px-2 py-2 border border-gray-200">
                        {item.teaching_level}
                      </td>
                      <td className="px-2 py-2 border border-gray-200">
                        {item.qualification_name}
                      </td>
                      <td className="px-2 py-2 border border-gray-200">
                        {item.position_name}
                      </td>
                      <td className="px-2 py-2 border border-gray-200">
                        {item.subjects.length > 0 ? (
                          <div
                            onClick={() =>
                              this.setState({ view_subjects: item })
                            }
                            className="bg-gray-100 px-3 py-2 rounded cursor-pointer hover:text-primary-800 text-sm w-max hover:bg-primary-100"
                          >
                            View subjects (
                            {
                              removeDuplicateParticipantsSubjects(item.subjects)
                                .length
                            }
                            )
                          </div>
                        ) : (
                          <div className="px-3">No subjects</div>
                        )}
                        {this.state.view_subjects !== null &&
                          this.state.view_subjects.nid === item.nid && (
                            <div
                              className="bg-white border rounded-md shadow-lg absolute right-0 animate__animated animate__zoomIn animate__faster"
                              style={{ zIndex: 9 }}
                            >
                              <div className="flex flex-row items-center justify-between gap-2 p-2 px-3">
                                <span className="font-bold text-lg">
                                  Subjects taught
                                </span>
                                <div>
                                  <div
                                    onClick={() =>
                                      this.setState({ view_subjects: null })
                                    }
                                    className="h-8 w-8 rounded-full bg-red-100 text-red-700 cursor-pointer hover:bg-red-200 flex items-center justify-center"
                                  >
                                    <MdClose className="text-2xl" />
                                  </div>
                                </div>
                              </div>
                              {/* List */}
                              <div className="p-2">
                                <table className="w-full text-sm text-left">
                                  <thead>
                                    <td className="px-3 py-2 border border-gray-300 font-bold">
                                      #
                                    </td>
                                    <td className="px-3 py-2 border border-gray-300 font-bold">
                                      Course code
                                    </td>
                                    <td className="px-3 py-2 border border-gray-300 font-bold">
                                      Course name
                                    </td>
                                  </thead>
                                  <tbody>
                                    {removeDuplicateParticipantsSubjects(
                                      this.state.view_subjects.subjects
                                    ).map((course, i) => (
                                      <tr key={i + 1} className={``}>
                                        <td className="px-3 py-2 border border-gray-300">
                                          {i + 1}
                                        </td>
                                        <td className="px-3 py-2 border border-gray-300">
                                          {course.course_code}
                                        </td>
                                        <td className="px-3 py-2 border border-gray-300">
                                          {course.course_name}
                                        </td>
                                      </tr>
                                    ))}
                                  </tbody>
                                </table>
                              </div>
                            </div>
                          )}
                      </td>
                      <td className="px-1 py-1 border border-gray-200 w-10">
                        <div
                          onClick={() => {
                            this.setState({ success: "", error: "" });
                            if (
                              this.props.addedParticipants.length <
                              this.props.training_details.total
                            ) {
                              this.AddNewParticipants(item);
                            } else {
                              this.setState({
                                error:
                                  "You can not override the limit which is " +
                                  this.props.training_details.total,
                                success: "",
                              });
                              alert(
                                "You can not override the limit which is " +
                                  this.props.training_details.total
                              );
                              this.props.onClose();
                            }
                            this.setState({ continue: true });
                          }}
                          className={`${
                            this.state.adding_participant !== null &&
                            this.state.adding_participant.user_id ===
                              item.user_id
                              ? "bg-primary-100 text-primary-800 cursor-wait"
                              : "bg-accent-400 text-accent-900 group-hover:bg-accent-800 group-hover:text-black cursor-pointer"
                          } text-sm font-bold w-max px-3 rounded py-2 float-right flex flex-row items-center justify-center gap-2`}
                        >
                          {this.state.adding_participant !== null &&
                            this.state.adding_participant.user_id ===
                              item.user_id && (
                              <div>
                                <AiOutlineLoading3Quarters className="text-xl animate-spin" />
                              </div>
                            )}
                          <span>
                            {this.state.adding_participant !== null &&
                            this.state.adding_participant.user_id ===
                              item.user_id
                              ? "Adding..."
                              : "Add participant"}
                          </span>
                        </div>
                      </td>
                    </tr>
                  ))
                )}
              </tbody>
            </table>
          </div>
        }
      </div>
    );
  }
}
