export const GetDPListByStatus = async (
  school_code: string,
  status: TrainingImplementationStatus,
  setResponse: (
    loading: boolean,
    res: {
      type: "success" | "error";
      msg: string;
      data: GetTrainingsListByStatusInterface[] | null;
    } | null
  ) => void
) => {
  setResponse(true, null);
  try {
    const res = await axios.get<GetTrainingsListByStatusInterface[]>(
      `${TRAINING_APP_URL}/training/school/${school_code}/${status}`
    );
    console.log("Res: ", res.data);
    if (res) {
      setResponse(false, {
        data: res.data,
        type: "success",
        msg: "Data loaded successfully",
      });
    }
  } catch (error: any) {
    setResponse(false, {
      data: null,
      type: "error",
      msg: returnErrorToText(error),
    });
    console.log("Err: ", { ...error });
  }
};

