export const AssignTraineeToTraining = async (
  data: AssignTraineeToTrainingDataInterface,
  setResponse: (
    loading: boolean,
    res: {
      type: "success" | "error";
      msg: string;
    } | null
  ) => void
) => {
  setResponse(true, null);
  try {
    const res = await axios.post(`${TRAINING_APP_URL}/training/trainee`, data);
    console.log("Assign trainee to training: ", res);
    if (res) {
      setResponse(false, {
        type: "success",
        msg: "Trainee added successfully!",
      });
    }
  } catch (error: any) {
    setResponse(false, {
      type: "error",
      msg: returnErrorToText(error),
    });
    console.log("Err: ", { ...error });
  }
};

export const RemoveTraineeToTraining = async (
  data: {
    training_id: string;
    user_id: string;
    status: "DISABLED";
  },
  setResponse: (
    loading: boolean,
    res: {
      type: "success" | "error";
      msg: string;
    } | null
  ) => void
) => {
  setResponse(true, null);
  try {
    const res = await axios.patch(
      `${TRAINING_APP_URL}/training/trainee/toggle`,
      data
    );
    console.log("Removing: ", res);
    if (res) {
      setResponse(false, {
        type: "success",
        msg: "Trainee added successfully!",
      });
    }
  } catch (error: any) {
    setResponse(false, {
      type: "error",
      msg: returnErrorToText(error),
    });
    console.log("Err: ", { ...error });
  }
};
