import React, { Component, Fragment } from "react";
import { AiOutlineHistory } from "react-icons/ai";
import {
  BsArrowUpRight,
  BsCalendar2Range,
  BsFillArrowRightCircleFill,
  BsInfoCircle,
} from "react-icons/bs";
import { FaChalkboard, FaChalkboardTeacher } from "react-icons/fa";
import { IoMdTime } from "react-icons/io";
import { MdClose, MdOutlineAddModerator } from "react-icons/md";
import { RiSearchLine } from "react-icons/ri";
import { connect } from "react-redux";
import { Auth, Basic } from "../../actions";
import {
  GetDPListByStatus,
  GetTrainingsListByStatusInterface,
  TrainingImplementationStatus,
} from "../../actions/training-offers";
import Button from "../../components/Buttons/Button";
import Loading from "../../components/Loading/Loading";
import { PopUpContainer } from "../../components/PopUpContainer/PopUpContainer";
import { StoreState } from "../../reducers";
import { DATE, search } from "../../utils/functions";
import { TrainingDetails } from "./Details/TrainingDetails";

interface TrainingOffersProps {}
interface TrainingOffersState {
  loading: boolean;
  selected_training: GetTrainingsListByStatusInterface | null;
  trainings_list: GetTrainingsListByStatusInterface[] | null;
  selected_status: TrainingImplementationStatus;
  error: string;
  school_code: string;
  search_value: string;
}

class _TrainingOffers extends Component<
  TrainingOffersProps,
  TrainingOffersState
> {
  constructor(props: TrainingOffersProps) {
    super(props);

    this.state = {
      loading: false,
      selected_training: null,
      trainings_list: null,
      selected_status: TrainingImplementationStatus.INPROGRESS,
      search_value: "",
      error: "",
      school_code: "280101",
    };
  }
  LoadAllTrainings = (status: TrainingImplementationStatus) => {
    this.setState({ loading: true, selected_training: null });
    // Loading data
    GetDPListByStatus(
      this.state.school_code,
      status,
      (
        loading: boolean,
        res: {
          type: "success" | "error";
          msg: string;
          data: GetTrainingsListByStatusInterface[] | null;
        } | null
      ) => {
        this.setState({ loading: loading });
        if (res !== null && res.type === "success") {
          this.setState({
            trainings_list: res.data,
            loading: false,
            error: "",
          });
        }
        if (res !== null && res.type === "error") {
          this.setState({ trainings_list: [], error: res.msg, loading: false });
        }
      }
    );
  };
  componentDidMount(): void {
    if (this.state.trainings_list === null) {
      this.LoadAllTrainings(TrainingImplementationStatus.DRAFT); // Change here to: this.state.selected_status
    }
  }

  render() {
    return (
      <Fragment>
        <div className="container mx-auto p-3 relative">
          {/* Header */}
          <div className="bg-white rounded p-3">
            <div className="grid grid-cols-12 gap-2">
              <div className="col-span-12 lg:col-span-3 flex flex-row items-center gap-2 px-2">
                <div>
                  <FaChalkboardTeacher className="text-3xl text-primary-800 animate__animated animate__tada" />
                </div>
                <div className="text-xl font-bold text-primary-800">
                  Training Offers
                </div>
              </div>
              <div className="col-span-12 md:col-span-6 lg:col-span-5">
                <div className="relative">
                  <div className="absolute px-3 py-2">
                    <RiSearchLine className="text-2xl text-gray-600" />
                  </div>
                  <input
                    type="search"
                    className="font-normal bg-gray-100 w-full px-3 pl-12 py-2 rounded"
                    placeholder="Search..."
                    value={this.state.search_value}
                    onChange={(e) =>
                      this.setState({ search_value: e.target.value })
                    }
                  />
                </div>
              </div>
              <div className="col-span-12 md:col-span-6 lg:col-span-4">
                <div className="flex flex-row items-center justify-end gap-2">
                  {[
                    TrainingImplementationStatus.INPROGRESS,
                    TrainingImplementationStatus.COMPLETED,
                  ].map((item, i) => (
                    <div
                      onClick={() => {
                        if (this.state.loading === false) {
                          this.setState({
                            selected_status: item,
                            trainings_list: null,
                          });
                          this.LoadAllTrainings(item);
                        }
                      }}
                      key={i + 1}
                      className={`rounded-md px-1 py-1 pr-3 mt-1 flex flex-row items-center gap-2 ${
                        item === this.state.selected_status
                          ? "bg-primary-100 text-primary-800 font-bold animate__animated animate__fadeIn"
                          : "hover:text-primary-800 hover:bg-primary-100 text-black bg-white"
                      } cursor-pointer group`}
                    >
                      <div>
                        <div
                          className={`h-7 w-7 ${
                            item === this.state.selected_status
                              ? "bg-white text-primary-800"
                              : "bg-primary-100 text-primary-700 group-hover:bg-primary-800 group-hover:text-white"
                          } rounded-full flex items-center justify-center`}
                        >
                          {item === TrainingImplementationStatus.INPROGRESS ? (
                            <FaChalkboard className="text-md" />
                          ) : (
                            <AiOutlineHistory className="text-md" />
                          )}
                        </div>
                      </div>
                      <div>
                        {item === TrainingImplementationStatus.INPROGRESS
                          ? "New trainings"
                          : "History"}
                      </div>
                    </div>
                  ))}
                </div>
              </div>
            </div>
          </div>
          <div className="bg-white my-2 rounded px-3 py-2 flex flex-row items-center justify-between gap-2">
            <div className="flex flex-row items-center gap-2">
              <div>
                <BsInfoCircle className="text-2xl text-accent-900" />
              </div>
              <span className="text-gray-500 font-light">
                New upcoming training offers, please you are required to assign
                teacher to the training
              </span>
            </div>
            <div>
              <MdClose className="text-xl" />
            </div>
          </div>
          {this.state.error !== "" && (
            <div
              onClick={() => this.setState({ error: "" })}
              className="rounded px-3 py-2 bg-red-50 text-red-700 border border-white my-3 animate__animated animate__shakeX shadow flex flex-row items-center justify-between gap-2 cursor-pointer"
            >
              <span>{this.state.error}</span>
              <div>
                <MdClose className="text-xl" />
              </div>
            </div>
          )}
          {/* Body */}
          {this.state.trainings_list === null || this.state.loading === true ? (
            <div className="flex flex-col items-center justify-center bg-white rounded-md p-6">
              <Loading />
              <div className="text-center text-lg font-light">
                Loading, please wait
              </div>
            </div>
          ) : this.state.trainings_list.length === 0 ? (
            <div className="flex flex-col items-center justify-center bg-white rounded-md p-6">
              <div className="mb-2">
                <BsCalendar2Range className="text-6xl text-gray-300 animate__animated animate__shakeX" />
              </div>
              <div className="text-xl font-bold">No trainings available</div>
              <div className="text-sm">
                No training offers available for this school's teachers
              </div>
            </div>
          ) : (
              search(
                this.state.trainings_list,
                this.state.search_value
              ) as GetTrainingsListByStatusInterface[]
            ).length === 0 ? (
            <div className="bg-white rounded-md p-3 py-5 text-center text-xl font-light animate__animated animate__fadeIn">
              <span>No result found!</span>
            </div>
          ) : (
            (
              search(
                this.state.trainings_list,
                this.state.search_value
              ) as GetTrainingsListByStatusInterface[]
            ).map((item, i) => (
              <div
                key={i + 1}
                className="bg-white border border-primary-300 hover:border-primary-500 mt-3 rounded-md flex flex-col lg:flex-row items-end lg:items-center justify-between gap-2 p-4 group animate__animated animate__fadeIn"
              >
                <div className="flex flex-col md:flex-row gap-5 w-full">
                  {/* Icon here */}
                  <div>
                    <div className="relative bg-accent-400 text-accent-900 rounded-lg p-4 h-24 w-24">
                      <div className="px-1 rounded-xl bg-primary-700 text-white w-max font-bold absolute text-xs top-0 right-0 -mt-2 -mr-2 animate__animated animate__fadeIn">
                        New
                      </div>
                      <div>
                        <MdOutlineAddModerator className="text-6xl" />
                      </div>
                    </div>
                  </div>
                  {/* Details here */}
                  <div className="flex flex-col">
                    <div className="text-lg font-bold mb-1">{item.title}</div>

                    <div className="text-sm text-black font-light">
                      {item.description}
                    </div>
                    {/* Time date */}
                    <div className="flex flex-row items-center gap-4 mt-2">
                      {/* Start date */}
                      <div className="flex flex-row items-center gap-2">
                        <div>
                          <IoMdTime className="text-3xl text-accent-900" />
                        </div>
                        <div className="flex flex-col">
                          <div className="text-sm text-gray-500">
                            Start date
                          </div>
                          <div className="font-bold text-sm">
                            {DATE(item.start_date, "DD/MM/YYYY")}
                          </div>
                        </div>
                      </div>
                      {/* ........ */}
                      <div className="flex flex-row items-center gap-3">
                        <div className="h-3 w-3 rounded-full bg-gray-300 group-hover:bg-accent-500"></div>
                        <div className="h-3 w-3 rounded-full bg-gray-300 group-hover:bg-accent-500"></div>
                        <div className="h-3 w-3 rounded-full bg-gray-300 group-hover:bg-accent-500"></div>
                        <div className="hidden md:block h-3 w-3 rounded-full bg-gray-300 group-hover:bg-accent-500"></div>
                        <div className="hidden md:block h-3 w-3 rounded-full bg-gray-300 group-hover:bg-accent-500"></div>
                        <div className="hidden md:block h-3 w-3 rounded-full bg-gray-300 group-hover:bg-accent-500"></div>
                      </div>
                      {/* Ending date */}
                      <div className="flex flex-row items-center gap-2">
                        <div>
                          <IoMdTime className="text-3xl text-accent-900" />
                        </div>
                        <div className="flex flex-col">
                          <div className="text-sm text-gray-500">
                            Ending date
                          </div>
                          <div className="font-bold text-sm">
                            {DATE(item.end_date, "DD/MM/YYYY")}
                          </div>
                        </div>
                      </div>
                    </div>
                    <div className="flex flex-row items-center gap-2 mt-3">
                      <div>
                        <BsFillArrowRightCircleFill className="text-accent-800" />
                      </div>
                      <span className="text-sm">
                        Provider: {item.development_partner_name}
                      </span>
                    </div>
                  </div>
                </div>
                <div className="">
                  <Button
                    className="w-40 bg-accent-400 border-accent-500 group-hover:bg-accent-800 group-hover:border-accent-800 group-hover:text-black flex flex-row items-center justify-center gap-2"
                    theme="accent-light"
                    onClick={() => this.setState({ selected_training: item })}
                  >
                    <span>More details</span>
                    <div>
                      <BsArrowUpRight className="text-xl" />
                    </div>
                  </Button>
                </div>
              </div>
            ))
          )}
          {/* Details page */}
          {this.state.selected_training !== null && (
            <PopUpContainer className="p-3 px-4">
              <TrainingDetails
                training_details={this.state.selected_training}
                onClose={() => this.setState({ selected_training: null })}
                school_code={this.state.school_code}
              />
            </PopUpContainer>
          )}
        </div>
      </Fragment>
    );
  }
}

const mapStateToProps = ({
  auth,
  basic,
}: StoreState): { auth: Auth; basic: Basic } => {
  return { auth: auth, basic: basic };
};

export const TrainingOffers = connect(mapStateToProps, {})(_TrainingOffers);
